.division-container{
    display: flex;
    justify-content: space-between;
    margin-top: 10mm;
}

.division-container-second {
    margin-top: 30mm;
}


.division{
    position: relative;
    display: flex;
    gap: 5mm;
    width: 30mm;
    height: 30mm;
    padding-left: 2mm;
    padding-right: 2mm;
}



.division-left-p{
    margin: 0;
    text-align: left;
    font-size: 28px;
}

.division-right-p{
    margin: 0;
    margin-left: 1mm;
    text-align: left;
    font-size: 28px;
}

.division-line-1 {
    position: absolute;
    top: 0;
    left: 15mm;
    width: 2px;
    height: 30mm;
    background-color: black;
}

.division-line-2 {
    position: absolute;
    top:10mm;
    left: 15mm;
    width: 20mm;
    height: 2px;
    background-color: black;
}
