.addition-container{
    display: flex;
    justify-content: space-between;
    margin-top: 10mm;
}

.addition{
    position: relative;
    width: 30mm;
    height: 30mm;
    padding-left: 2mm;
    padding-right: 2mm;
}

.addition p{
    margin: 0;
    text-align: right;
    font-size: 28px;
}

.addition-bottom{
    position: absolute;
    bottom: 10mm;
    right: 0;
}

.addition-bottom p {
    text-align: left;
    margin: 0;
}

.addition-line {
    width: 25mm;
    height: 2px;
    background-color: black;
}